import React, { useEffect, useMemo, useRef, useState } from 'react';
import api from '../../services/api';

const Inquiries = ({ isOpen, onClose }) => {
  const [activeTab, setActiveTab] = useState('chats');
  const [selectedChatId, setSelectedChatId] = useState(null);
  const [newMessage, setNewMessage] = useState('');
  const [inquiries, setInquiries] = useState([]); // inquiries from backend API
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showAssignModal, setShowAssignModal] = useState(false);
  const [assigning, setAssigning] = useState(false);
  const hydratedRef = useRef(false);

  // Load inquiries from backend API
  const loadInquiries = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await api.getManagerInquiries();
      if (response && response.inquiries) {
        const mapped = response.inquiries.map(inquiry => ({
          id: inquiry.id,
          tenantName: inquiry.tenant_name,
          tenantEmail: inquiry.tenant_email,
          tenantPhone: inquiry.tenant_phone,
          property: inquiry.property?.title || inquiry.property?.building_name || 'Property',
          propertyId: inquiry.property_id,
          unitName: inquiry.unit_name,
          status: inquiry.status,
          messages: (function(){
            const regex = /\n\n--- New Message(?: \[(\d{10,})\])? ---\n/g;
            const src = String(inquiry.message || '');
            const chunks = []; let last = 0; let m;
            while ((m = regex.exec(src)) !== null) {
              const txt = src.slice(last, m.index);
              if (txt) chunks.push({ text: txt, ts: null });
              chunks.push({ text: null, ts: m[1] ? Number(m[1]) : null });
              last = m.index + m[0].length;
            }
            const tail = src.slice(last); if (tail) chunks.push({ text: tail, ts: null });
            const out = []; let pending = null; const now = Date.now();
            for (const c of chunks) {
              if (c.text === null) { pending = c.ts; continue; }
              out.push({ id: `${inquiry.id}-t-${out.length}`, sender: 'tenant', text: c.text, time: new Date(pending || now).toLocaleTimeString([], { hour: 'numeric', minute: '2-digit' }) });
              pending = null;
            }
            // Parse manager replies split by our timestamped separators
            if (inquiry.response_message) {
              const mSep = /\n\n--- Manager Reply \[(\d{10,})\] ---\n/g;
              const mSrc = String(inquiry.response_message || '');
              const mParts = []; let mLast = 0; let mm;
              while ((mm = mSep.exec(mSrc)) !== null) {
                const txt = mSrc.slice(mLast, mm.index);
                if (txt) mParts.push({ text: txt, ts: null });
                mParts.push({ text: null, ts: mm[1] ? Number(mm[1]) : null });
                mLast = mm.index + mm[0].length;
              }
              const mTail = mSrc.slice(mLast); if (mTail) mParts.push({ text: mTail, ts: null });
              let mPending = null;
              for (const part of mParts) {
                if (part.text === null) { mPending = part.ts; continue; }
                out.push({ id: `${inquiry.id}-m-${out.length}`, sender: 'manager', text: part.text, time: new Date(mPending || now).toLocaleTimeString([], { hour: 'numeric', minute: '2-digit' }) });
                mPending = null;
              }
            }
            return out;
          })(),
          inquiry: inquiry
        }));
        // Deduplicate by propertyId to ensure a single row per property
        const dedup = [];
        const seen = new Set();
        for (const c of mapped) {
          const key = String(c.propertyId);
          if (seen.has(key)) continue;
          seen.add(key);
          dedup.push(c);
        }
        setInquiries(dedup);
      }
    } catch (err) {
      console.error('Failed to load inquiries:', err);
      setError('Failed to load inquiries. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  // Load inquiries on mount
  useEffect(() => {
    loadInquiries();
    hydratedRef.current = true;
  }, []);

  // Persist PM selection back so it opens same thread again
  useEffect(() => {
    if (!hydratedRef.current) return;
    try { if (selectedChatId) localStorage.setItem('pm_selected_chat_id', selectedChatId); } catch (_) {}
  }, [selectedChatId]);

  // Debug modal state
  useEffect(() => {
    console.log('showAssignModal state changed:', showAssignModal);
  }, [showAssignModal]);

  const selectedChat = useMemo(() => inquiries.find(c => c.id === selectedChatId) || null, [inquiries, selectedChatId]);

  const handleSendMessage = async () => {
    if (!newMessage.trim() || !selectedChat) return;
    
    try {
      setLoading(true);
      const response = await api.sendManagerMessage(selectedChat.id, newMessage.trim());
      
      if (response && response.success) {
        // Add the message to the inquiry
        setInquiries(prev => prev.map(c => c.id === selectedChat.id ? {
          ...c,
          messages: [...(c.messages || []), { 
            id: Date.now(), 
            sender: 'manager', 
            text: newMessage.trim(), 
            time: new Date().toLocaleTimeString([], { hour: 'numeric', minute: '2-digit' }) 
          }]
        } : c));
        setNewMessage('');
      } else {
        setError('Failed to send message. Please try again.');
      }
    } catch (err) {
      console.error('Failed to send message:', err);
      setError('Failed to send message. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const markAsRead = (inquiryId) => {
    // no-op for local prototype; could update a flag in local storage
  };

  const handleAssignTenant = async () => {
    console.log('handleAssignTenant called');
    if (!selectedChat) {
      console.log('No selectedChat found');
      return;
    }
    
    try {
      console.log('Starting assignment process');
      setAssigning(true);
      setError(null);
      
      // Call API to assign tenant to property
      console.log('Calling API with inquiry ID:', selectedChat.id, 'property ID:', selectedChat.propertyId);
      const response = await api.assignTenantToProperty(selectedChat.id, selectedChat.propertyId);
      
      if (response && response.success) {
        // Update the inquiry status in the local state
        setInquiries(prev => prev.map(inquiry => 
          inquiry.id === selectedChat.id 
            ? { ...inquiry, status: 'assigned' }
            : inquiry
        ));
        
        setShowAssignModal(false);
        // Show success message
        setError(null);
        // You could add a success notification here
        console.log('Assignment successful:', response.message);
      } else {
        setError('Failed to assign tenant. Please try again.');
      }
    } catch (err) {
      console.error('Failed to assign tenant:', err);
      setError('Failed to assign tenant. Please try again.');
    } finally {
      setAssigning(false);
    }
  };

  if (!isOpen) return null;

  if (loading) {
    return (
      <div className="fixed inset-0 z-50 flex items-center justify-center">
        <div className="bg-black bg-opacity-50 fixed inset-0" onClick={onClose}></div>
        <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full mx-4 h-[80vh] flex items-center justify-center relative z-10">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
            <p className="text-gray-600">Loading inquiries...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      {/* Backdrop */}
      <div className="bg-black bg-opacity-50 fixed inset-0" onClick={onClose}></div>
      
      {/* Modal Content */}
      <div className="bg-white rounded-lg shadow-xl max-w-6xl w-full mx-4 h-[80vh] flex flex-col relative z-10">
        {/* Header */}
        <div className="p-6 border-b flex items-center justify-between">
          <div>
            <h2 className="text-xl font-semibold text-gray-900">Property Inquiries</h2>
            <p className="text-sm text-gray-600 mt-1">Manage tenant inquiries for your properties</p>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border-l-4 border-red-400 p-4 mx-6 mt-2">
            <div className="flex">
              <div className="ml-3">
                <p className="text-sm text-red-700">{error}</p>
                <button 
                  onClick={() => setError(null)}
                  className="text-xs text-red-600 underline mt-1"
                >
                  Dismiss
                </button>
              </div>
            </div>
          </div>
        )}

      {/* Tabs */}
      <div className="flex border-b">
        <button
          onClick={() => setActiveTab('chats')}
          className={`px-6 py-3 font-medium ${
            activeTab === 'chats'
              ? 'text-blue-600 border-b-2 border-blue-600'
              : 'text-gray-500 hover:text-gray-700'
          }`}
        >
          Active Chats ({inquiries.length})
        </button>
        <button
          onClick={() => setActiveTab('new')}
          className={`px-6 py-3 font-medium ${
            activeTab === 'new'
              ? 'text-blue-600 border-b-2 border-blue-600'
              : 'text-gray-500 hover:text-gray-700'
          }`}
        >
          New Inquiries ({inquiries.filter(i => i.status === 'new').length})
        </button>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-hidden">
        {activeTab === 'chats' ? (
          <div className="flex h-full">
            {/* Chat List */}
            <div className="w-1/3 border-r overflow-y-auto">
                {inquiries.length === 0 ? (
                <div className="p-6 text-center text-gray-500">
                  <div className="mb-4">
                    <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-3.582 8-8 8a8.959 8.959 0 01-4.906-1.456L3 21l2.456-5.094A8.959 8.959 0 013 12c0-4.418 3.582-8 8-8s8 3.582 8 8z" />
                    </svg>
                  </div>
                  <p>No inquiries yet.</p>
                  <p className="text-sm mt-1">Inquiries will appear here when tenants contact you about your properties.</p>
                </div>
                ) : (
                inquiries.map((inquiry) => (
                  <div
                    key={inquiry.id}
                    onClick={() => {
                      setSelectedChatId(inquiry.id);
                      if (inquiry.unread_count > 0) {
                        markAsRead(inquiry.id);
                      }
                    }}
                    className={`p-4 border-b cursor-pointer hover:bg-gray-50 ${
                      selectedChatId === inquiry.id ? 'bg-blue-50 border-blue-200' : ''
                    }`}
                  >
                    <div className="flex items-center space-x-3">
                      <div className="w-10 h-10 bg-gray-100 rounded-full flex items-center justify-center">
                        <span className="text-gray-600 font-medium">
                          {(inquiry.avatar || (inquiry.managerName || 'PM')).toString().slice(0,2).toUpperCase()}
                        </span>
                      </div>
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium text-gray-900 truncate">
                          {inquiry.managerName || 'Property Manager'}
                        </p>
                        <p className="text-sm text-gray-500 truncate">
                          {inquiry.unitName ? `${inquiry.unitName}/${inquiry.property}` : inquiry.property || 'Property'}
                        </p>
                        <p className="text-xs text-gray-400">
                          {inquiry.messages?.[inquiry.messages.length - 1]?.time || 'No messages'}
                        </p>
                      </div>
                      <div className="flex flex-col items-end space-y-1">
                        {inquiry.unread_count > 0 && (
                          <span className="bg-blue-600 text-white text-xs rounded-full px-2 py-1">
                            {inquiry.unread_count}
                          </span>
                        )}
                        <span className={`text-xs px-2 py-1 rounded-full ${
                          inquiry.status === 'new' ? 'bg-green-100 text-green-800' :
                          inquiry.status === 'active' ? 'bg-blue-100 text-blue-800' :
                          inquiry.status === 'assigned' ? 'bg-purple-100 text-purple-800' :
                          inquiry.status === 'responded' ? 'bg-yellow-100 text-yellow-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {inquiry.status || 'active'}
                        </span>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>

            {/* Chat Messages */}
            <div className="flex-1 flex flex-col">
              {selectedChat ? (
                <>
                  {/* Chat Header */}
                  <div className="p-4 border-b bg-gray-50">
                    <div className="flex items-center justify-between">
                      <div>
                        <h3 className="font-medium text-gray-900">{selectedChat.tenantName || selectedChat.tenant?.name}</h3>
                        <p className="text-sm text-gray-500">
                          Interested in: {selectedChat.unitName ? `${selectedChat.unitName}/${selectedChat.property}` : selectedChat.property || 'Property'}
                        </p>
                        {(selectedChat.tenantEmail || selectedChat.tenant?.email || selectedChat.tenantPhone || selectedChat.tenant?.phone) && (
                          <p className="text-xs text-gray-400">
                            {[selectedChat.tenantEmail || selectedChat.tenant?.email, selectedChat.tenantPhone || selectedChat.tenant?.phone].filter(Boolean).join('  ')}
                          </p>
                        )}
                      </div>
                      <div className="flex items-center space-x-3">
                        {selectedChat.status !== 'assigned' && (
                          <button
                            onClick={() => {
                              console.log('Assign to Tenant button clicked');
                              setShowAssignModal(true);
                            }}
                            className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 text-sm font-medium transition-colors"
                          >
                            Assign to Tenant
                          </button>
                        )}
                        {selectedChat.status === 'assigned' && (
                          <span className="bg-purple-100 text-purple-800 px-3 py-2 rounded-lg text-sm font-medium">
                             Assigned
                          </span>
                        )}
                        <div className="text-right">
                          {selectedChat.price && (
                            <p className="text-sm font-medium text-blue-600">
                              {selectedChat.price}
                            </p>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Messages */}
                  <div className="flex-1 overflow-y-auto p-4 space-y-4">
                    {selectedChat.messages?.length === 0 ? (
                      <div className="text-center text-gray-500 py-8">
                        <p>No messages yet. The tenant is waiting for your response!</p>
                      </div>
                    ) : (
                      (selectedChat.messages || []).map((message) => (
                        <div
                          key={message.id}
                          className={`flex ${message.sender === 'manager' ? 'justify-end' : 'justify-start'}`}
                        >
                          <div
                            className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                              message.sender === 'manager' ? 'bg-blue-600 text-white' : 'bg-gray-200 text-gray-900'
                            }`}
                          >
                            <p className="text-sm">{message.text || message.content}</p>
                            <p className={`text-xs mt-1 ${message.sender === 'manager' ? 'text-blue-100' : 'text-gray-500'}`}>
                              {message.time || (message.created_at ? new Date(message.created_at).toLocaleTimeString() : '')}
                            </p>
                          </div>
                        </div>
                      ))
                    )}
                  </div>

                  {/* Message Input */}
                  <div className="p-4 border-t">
                    <div className="flex space-x-2">
                      <input
                        type="text"
                        value={newMessage}
                        onChange={(e) => setNewMessage(e.target.value)}
                        placeholder="Type your response..."
                        className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-black placeholder-gray-400"
                        onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
                      />
                      <button
                        onClick={handleSendMessage}
                        disabled={!newMessage.trim()}
                        className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        Send
                      </button>
                    </div>
                  </div>
                </>
              ) : (
                <div className="flex-1 flex items-center justify-center text-gray-500">
                  <div className="text-center">
                    <svg className="mx-auto h-12 w-12 text-gray-400 mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-3.582 8-8 8a8.959 8.959 0 01-4.906-1.456L3 21l2.456-5.094A8.959 8.959 0 013 12c0-4.418 3.582-8 8-8s8 3.582 8 8z" />
                    </svg>
                    <p>Select an inquiry to view messages</p>
                  </div>
                </div>
              )}
            </div>
          </div>
        ) : (
          /* New Inquiries Tab */
          <div className="p-6 overflow-y-auto">
            <div className="space-y-4">
              {inquiries.filter(i => i.status === 'new').length === 0 ? (
                <div className="text-center text-gray-500 py-8">
                  <div className="mb-4">
                    <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2 2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
                    </svg>
                  </div>
                  <p>No new inquiries</p>
                  <p className="text-sm mt-1">New tenant inquiries will appear here.</p>
                </div>
              ) : (
                inquiries.filter(i => i.status === 'new').map((inquiry) => (
                  <div key={inquiry.id} className="border rounded-lg p-4 hover:shadow-md transition-shadow">
                    <div className="flex items-start space-x-4">
                      <img
                        src={inquiry.property?.images?.[0] || '/placeholder-property.jpg'}
                        alt={inquiry.property?.title}
                        className="w-16 h-16 object-cover rounded-lg"
                      />
                      <div className="flex-1">
                        <div className="flex items-start justify-between">
                          <div>
                            <h4 className="font-medium text-gray-900">{inquiry.tenant?.name}</h4>
                            <p className="text-sm text-gray-600">{inquiry.tenant?.email}</p>
                            <p className="text-sm text-gray-500 mt-1">
                              Interested in: <span className="font-medium">{inquiry.unitName ? `${inquiry.unitName}/${inquiry.property}` : inquiry.property || 'Property'}</span>
                            </p>
                          </div>
                          <div className="text-right">
                            <span className="bg-green-100 text-green-800 text-xs px-2 py-1 rounded-full">
                              New
                            </span>
                            <p className="text-xs text-gray-500 mt-1">
                              {new Date(inquiry.created_at).toLocaleDateString()}
                            </p>
                          </div>
                        </div>
                        <div className="mt-3 flex space-x-2">
                          <button
                            onClick={() => {
                              setSelectedChatId(inquiry.id);
                              setActiveTab('chats');
                              markAsRead(inquiry.id);
                            }}
                            className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 text-sm"
                          >
                            Respond
                          </button>
                          <button className="bg-gray-100 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-200 text-sm">
                            View Property
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        )}
      </div>
      </div>

      {/* Assign Tenant Confirmation Modal */}
      {showAssignModal && (
        <div className="fixed inset-0 z-[100] flex items-center justify-center">
          {/* Backdrop */}
          <div 
            className="absolute inset-0 bg-black bg-opacity-50" 
            onClick={() => setShowAssignModal(false)}
          ></div>
          
          {/* Modal Content */}
          <div className="relative bg-white rounded-lg shadow-xl max-w-md w-full mx-4 z-10">
            <div className="p-6">
              {/* Header */}
              <div className="flex items-center mb-4">
                <div className="w-10 h-10 bg-green-100 rounded-full flex items-center justify-center mr-3">
                  <svg className="w-6 h-6 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                  </svg>
                </div>
                <h3 className="text-lg font-semibold text-gray-900">Assign Tenant to Unit</h3>
              </div>
              
              {/* Content */}
              <div className="mb-6">
                <p className="text-gray-600 mb-4">
                  Are you sure you want to assign <strong>{selectedChat?.tenantName || selectedChat?.tenant?.name || 'this tenant'}</strong> to <strong>{selectedChat?.unitName || 'a unit'}</strong> in <strong>{selectedChat?.property || 'this property'}</strong>?
                </p>
                
                <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-3">
                  <p className="text-sm text-yellow-800">
                    <strong>Warning:</strong> This will change the unit status from vacant to occupied and create a tenancy record.
                  </p>
                </div>
              </div>
              
              {/* Actions */}
              <div className="flex space-x-3">
                <button
                  onClick={() => setShowAssignModal(false)}
                  disabled={assigning}
                  className="flex-1 bg-gray-100 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAssignTenant}
                  disabled={assigning}
                  className="flex-1 bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center transition-colors"
                >
                  {assigning ? (
                    <>
                      <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                      </svg>
                      Assigning...
                    </>
                  ) : (
                    'Confirm Assignment'
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Inquiries;